import Vue from 'vue'
import Router from 'vue-router'
import game from './views/game.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect: '/game'
    },
    {
      path: '/game',
      name: 'game',
      component: () => import( './views/game.vue')
    },
  ]
})
