<template>
	<div class="tk">
		<div class="box">
			<img :src="tkb" :class="{active:isactive}">
			<span v-for="item in tka" @click="tkchange(item)">
				<img :src="item.imgurl">
			</span>
			<cube-button @click="changecolor(
			)">{{bian}}</cube-button>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				tka:[
					{
						imgurl: require ('../../assets/images/0.jpg')
					},
					{
						imgurl: require ('../../assets/images/1.jpg')
					},
					{
						imgurl: require ('../../assets/images/2.jpg')
					},
					{
						imgurl: require ('../../assets/images/3.jpg')
					},
					{
						imgurl: require ('../../assets/images/4.jpg')
					},
				],
				tkb:require ('../../assets/images/0.jpg'),
				isactive:false,
				bian:'变暗'
			}
		},
		methods:{
			tkchange(item){
				this.tkb = item.imgurl 
			},
			changecolor(){
				this.isactive = !this.isactive
				if (this.isactive) {
					this.bian = '变亮'
				}
				if (!this.isactive) {
					this.bian = '变暗'
				}
			}
		}
	}
</script>

<style lang="scss">
	.tk{
		.box{
			img{
				width: 100%;
				margin-bottom: .2rem;
			}
			.active{
				filter: grayscale(100%);
			}
			span{

				font-size: 0;
				img{
					width: 20%;
				}
			}
			.cube-btn{
				margin-top: .2rem;
			}
		}
	}
</style>